.filters {
    padding: 2em;
    padding-top: 1em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;

    margin-top: 1em;
}

.filter {
    width: 80%;
    margin-left: 10%;
    padding: 1em;
    margin-top: 1em;
}

.filter p {
    width: 110px;
    text-align: left;
    margin-left: 1em;
    margin-top: 0.5em;
    cursor: pointer;
    -webkit-transition: color 0.1s linear;
    -o-transition: color 0.1s linear;
    transition: color 0.1s linear;
    font-size: 16px;
    display: inline-block;
}

.filter p:hover {
    color: #dda09a;
    -webkit-transition: color 0.2s linear;
    -o-transition: color 0.2s linear;
    transition: color 0.2s linear;
}

.filter .search-button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    margin-top: 1em;
}


