footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 6em;
    background: rgb(0, 0, 0);
}

footer .social {
    margin: 1em;
}

footer .social ul {
    display: block;
    margin-top: 2em;
}

footer .social ul li {
    margin-top: 0.5em;
    text-align: left;
    list-style-type: none;
}

footer .social .header {
    color: #9addb1;
    font-size: 18px;
}

footer {
    color: white;
}

footer .contact form {
    margin: 1em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    width: 300px;
}

footer input[type="text"] {
    color: #dda09a;
    padding: 0.75em;
    border: none;
    outline: none;
    text-align: left;
    background: #000000;
    border: 1px solid #dda09a;
}

footer textarea {
    color: #dda09a;
    padding: 0.75em;
    border: none;
    outline: none;
    text-align: left;
    background: #000000;
    resize: none;
    border: 1px solid #dda09a;
    height: 100px;
}

footer label {
    margin-top: 1em;
    margin-bottom: 0.25em;
    text-align: left;
}

footer input[type="submit"] {
    margin-top: 1em;
    padding: 15px;
    background: none;
    color: #dda09a;
    font-size: 14px;
    cursor: pointer;
    -webkit-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    outline: 0;
    border: 0;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: bold;
    border: 2px solid #dda09a;
}
  
footer input[type="submit"]:hover {
    background: #dda09a;
    color: #3b3b3b;
}

footer input[type="submit"]:active {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    background: hsl(5, 50%, 84%);
    border: 2px solid hsl(5, 50%, 84%);
}

footer ::-webkit-input-placeholder {
    color: hsl(5, 10%, 54%);
}

footer :-ms-input-placeholder {
    color: hsl(5, 10%, 54%);
}

footer ::-ms-input-placeholder {
    color: hsl(5, 10%, 54%);
}

footer ::placeholder {
    color: hsl(5, 10%, 54%);
}


