.slider {
    -ms-flex: 0 0 275px;
        flex: 0 0 275px;
    padding: 1em;
    margin-left: 1em;
    margin-right: 1em;
}

.slider label {
    color: #3b3b3b;
    font-size: 16px;
    margin-bottom: 2em;
    display: block;
}

.input-range__slider {
    background: #dda09a;
    border: none;
}

.input-range__track {
    background: #ddd;
}

.input-range__track--active {
    background: #dda09a;
}

.input-range__label--value .input-range__label-container {
    background: #dda09a;
    color: #3b3b3b;
    font-size: 12px;
    padding: 2px 5px;
    border-radius: 2px;
}

.input-range__label--min .input-range__label-container,
.input-range__label--max .input-range__label-container
{
    font-size: 12px;
    color: #3b3b3b;
    left: 0;
}

.input-range__label--max .input-range__label-container {
    left: 25%;
}


