.notFound {
    padding-top: 17em;
    padding-bottom: 17em;

    background: url("https://wallpaperscraft.com/image/spongebob_patrick_baseball_bat_art_106038_1920x1080.jpg");
    background-size: cover;
}

.notFound h3 {
    color: white;
    margin-bottom: 1em;
}

.notFound h2 {
    color: white;
    font-size: 40px;
    letter-spacing: 5px;
}

.notFound a {
    color: #9addb1;
}

.notFound a:hover {
    color: hsl(141, 50%, 84%);
}

.notFound > div {
    max-width: 440px;
    height: 100%;
    display: inline-block;

    background: rgba(0, 0, 0, 0.7);
    -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
            box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
    padding: 2em;
}


