.search-button {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.search-button button {
    padding: 10px 21px;
    background: none;
    color: #3b3b3b;
    font-size: 14px;
    cursor: pointer;
    -webkit-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    outline: 0;
    border: 0;
    
    border: 2px solid #dda09a;
}
  
.search-button button:hover {
    background: #dda09a;
}

.search-button button:active {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    background: hsl(5, 50%, 84%);
    border: 2px solid hsl(5, 50%, 84%);
}


