.navigation ul li {
    display: inline-block;
}

.navigation ul li a {
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: bold;
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
    padding-top: 2.9em;
    padding-bottom: 2.9em;
    padding-left: 1em;
    padding-right: 1em;
}

.navigation ul li a:hover {
    background: #9addb1;
    -webkit-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;
}

.navigation {
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
}

.navigation .active {
    background: #9addb1;
}

.hamburger .menuButton {
    z-index: 1005;
    color: #3b3b3b;
    font-size: 2em;
    background: none;
    border: none;
    outline: none;
    -webkit-transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
    -o-transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
    transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
}

.hamburger .menuButton:hover {
    color: #9addb1;
    -webkit-transform: scale(1.2, 1.2);
        -ms-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
    cursor: pointer;
}

.hamburger .menuButton:active {
    color: #9addb1;
}

.hamburger > .menu {
    width: 300px;
    height: 100vh;
    background-color: white;
    border-left: 3px solid #9addb1;
    position: fixed;
    top: 0;
    right: 0;
    -webkit-transition: -webkit-transform .3s cubic-bezier(0, .52, 0, 1);
    transition: -webkit-transform .3s cubic-bezier(0, .52, 0, 1);
    -o-transition: transform .3s cubic-bezier(0, .52, 0, 1);
    transition: transform .3s cubic-bezier(0, .52, 0, 1);
    transition: transform .3s cubic-bezier(0, .52, 0, 1), -webkit-transform .3s cubic-bezier(0, .52, 0, 1);
    overflow: scroll;
    z-index: 1000;
}

.hamburger > .hide {
    -webkit-transform: translate3d(+300px, 0, 0);
            transform: translate3d(+300px, 0, 0);
}

.hamburger > .show {
    -webkit-transform: translate3d(0px, 0, 0);
            transform: translate3d(0px, 0, 0);
    overflow: hidden;
}

.hamburger > .menu {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.hamburger > .menu > a:first-of-type {
    margin-top: 2em;
}

.hamburger > .menu > a {
    padding: 0.75em;
    font-size: 20px;
    font-weight: bold;
    text-align: left;
    padding-left: 50px;
}

.menuCover {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 999;
    background: rgba(0, 0, 0, 0.2);
}

.menuCover.show {
    display: block;
}

.menuCover.hide {
    display: none;
}


