button {
    padding: 10px 21px;
    background: none;
    color: #3b3b3b;
    font-size: 14px;
    cursor: pointer;
    -webkit-transition: all 0.2s ease-out;
    -o-transition: all 0.2s ease-out;
    transition: all 0.2s ease-out;
    outline: 0;
    border: 0;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: bold;
    border: 2px solid #dda09a;
}
  
button:hover {
    background: #dda09a;
}

button:active {
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    background: hsl(5, 50%, 84%);
    border: 2px solid hsl(5, 50%, 84%); 
}

.firstSquare {
    top: 350px;
    -webkit-transform: rotate(-3deg);
        -ms-transform: rotate(-3deg);
            transform: rotate(-3deg);
}

.rotatedSquare {
    z-index: -1;
    position: absolute;
    left: -10vw;
    width: 150vw;
    height: 1200px;
    background: #9addb1;
}


